<template>
	<view class="checkin-wrap">
		<!-- 使用camera标签 -->
		<camera device-position="front" flash="off" class="camera" @error="error" v-if="showCamera"></camera>
		<image mode="widthFix" class="image" :src="photoPath" v-if="showImage"></image>
		<view class="operate-container">
			<button type="primary" class="btn" @tap="clickBtn" :disabled="canCheckin">{{ btnText }}</button>
			<button type="warn" class="btn" @tap="afresh" :disabled="canCheckin">重拍</button>
		</view>
		<view class="notice-container">
			<text class="notice">注意事项</text>
			<text class="desc">拍照签到的时候,必须要拍摄自己的正面照片，侧面照片会导致无法识别。另外拍照的时候不要戴墨镜或者帽子,避免影响拍照的准确度</text>
		</view>
	</view>
</template>

<script>
	//加载腾讯位置sdk
	const globalData = getApp().globalData
	let QQMapWx = require("../../utils/map/qqmap-wx-jssdk.min.js")
	let qqmapsdk;

	export default {
		data() {
			return {
				//工作日才能使用人脸识别
				canCheckin: false,
				photoPath: '',
				btnText: '拍照',
				showCamera: true,
				showImage: false
			}
		},
		onLoad() {
			qqmapsdk = new QQMapWx({
				key: globalData.tx_map_key
			})
		},
		methods: {
			//拍照信息的错误回调
			error(err) {
				console.error(err)
			},
			//点击拍照功能
			clickBtn() {
				//判断当前的文字内容，如果是拍照过的话会变成签到文字
				if (this.btnText === '拍照') {
					//创建并返回 camera 组件的上下文 cameraContext 对象。
					const ctx = uni.createCameraContext()
					ctx.takePhoto({
						quality: "high", //照片质量
						success: (res) => {
							this.photoPath = res.tempImagePath
							this.showCamera = false
							this.showImage = true
							this.btnText = '签到'
						}
					})
				} else { //签到按钮获取用户的位置
					console.log("diaoyong")
					uni.getLocation({
						type: "gcj02",
						success: (res) => {
							// let longitude = res.longitude; //经度
							// let latitude = res.latitude; //纬度
							const {
								longitude,
								latitude
							} = res
							console.log(longitude, latitude)
							//坐标位置解析由坐标到坐标所在位置的文字描述的转换
							qqmapsdk.reverseGeocoder({
								location: {
									latitude,
									longitude
								},
								success: (reverseRes) => {
									//用户地址
									let address = reverseRes.result.address
									//用户省市地区 地址部件，address不满足需求时可自行拼接
									let addressComponent = reverseRes.result.address_component
									let nation = addressComponent.nation
									let province = addressComponent.province
									let city = addressComponent.city
									let district = addressComponent.district
									//发起请求实现签到
									uni.uploadFile({
										url: globalData.requesturl + "/checkin",
										// 拍照的路径
										filePath: this.photoPath,
										//对应后端接收的参数
										name:'photo',
										// 请求头一定要加，否则 iOS 图片上传会失败 安卓 可有可无
										header: {
											'content-type': 'multipart/form-data',
											"token": uni.getStorageSync("wxuser").token
										},
										success: (loadRes) => {
											console.log("图片上传结果", loadRes)
										},
										fail: (err) => {
											console.log("失败",err)
										}
									})
								}
							})
						}
					})
				}


			},
			//照片重拍功能
			afresh() {
				this.showCamera = true
				this.showImage = false
				this.btnText = '拍照'
				this.photoPath = ''
			}
		}
	}
</script>

<style lang="scss">
	@import "@/pages/checkin/checkin.scss";
</style>
